:root {
  --font-family: "Onest", sans-serif;
  --content-width: 1140px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --light2-color: #e0e0e0;
  --light3-color: #ffffffb0;
  --black-color: #212121;
  --black2-color: rgb(0, 0, 0, .5);
  --grey-color: #757575;
  --grey2-color: #999;
  --accent-color: #ffdd2d;
  --accent-hover-color: #fff500;
  --bg-color: #f6f7f8;
}
@media (max-width: 680px) {
  :root {
    --content-width: 480px;
  }
}

@font-face {
  font-family: "Onest";
  src: url("../fonts/OnestRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Onest";
  src: url("../fonts/OnestMedium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Onest";
  src: url("../fonts/OnestBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}
html {
  font-family: var(--font-family, sans-serif);
  font-size: 16px;
  box-sizing: border-box;
  height: 100%;
  -webkit-text-size-adjust: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  background-color: var(--bg-color);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.button {
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  color: var(--black-color);
  background-color: var(--accent-color);
  backdrop-filter: blur(17px);
  border-radius: 15px;
  padding: 13px 24px;
  max-width: 171px;
  white-space: nowrap;
  overflow: hidden;
}
.button:hover {
  background-color: var(--accent-hover-color);
}
.button::after {
  content: "";
  margin-left: 60px;
  width: 50px;
  height: 300px;
  background: #fff;
  -webkit-animation-delay: 0.05s;
  animation-delay: 0.05s;
  position: absolute;
  left: -40px;
  top: -150px;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  -webkit-animation-name: slideme;
  animation-name: slideme;
  -webkit-animation-duration: 3s;
  animation-duration: 3s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  filter: blur(1px);
}

@keyframes slideme {
  0% {
    left: -30px;
    margin-left: 0;
  }
  30% {
    left: 110%;
    margin-left: 80px;
  }
  100% {
    left: 110%;
    margin-left: 80px;
  }
}
.review {
  color: var(--black-color);
}
.review__head {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  background: var(--light3-color);
  border-radius: 20px;
  padding: 15px;
  margin-bottom: 20px;
}
@media (max-width: 480px) {
  .review__head {
    flex-direction: column;
    align-items: center;
  }
}
.review__left {
  flex: 1;
}
@media (max-width: 480px) {
  .review__left {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
}
.review__logo {
  display: inline-block;
  margin-bottom: 10px;
}
.review__rating {
  display: flex;
  align-items: center;
  column-gap: 15px;
  flex-wrap: wrap;
}
@media (max-width: 480px) {
  .review__rating {
    justify-content: center;
  }
}
.review__rating-numb {
  font-weight: 500;
  font-size: 20px;
}
.review__rating-stars {
  display: flex;
  gap: 3px;
  height: 16px;
  margin: 5px 0 3px;
}
.review__rating-count {
  font-size: 12px;
  color: var(--grey-color);
}
.review__cards {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}

.review__card {
  background-color: var(--light-color);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  padding: 15px;
  max-width: 363px;
  min-height: 335px;
}
.review__card-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.review__card-left {
  display: flex;
  align-items: center;
  gap: 20px;
}
.review__card-avatar {
  border-radius: 50%;
}
.review__card-date {
  font-size: 0.667rem;
  line-height: 1.3rem;
  letter-spacing: 0.8px;
  color: var(--grey2-color);
}
.review__card-name {
  font-size: 1.2rem;
}
.review__card-title {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.3;
  color: var(--black-color);
  margin-bottom: 1.3rem;
}
.review__card-comment {
  font-size: 16px;
  line-height: 1.3;
  color: var(--black-color);
  margin-bottom: 15px;
  max-height: 187px;
  overflow: hidden;
}
.review__card-link {
  font-size: 12px;
  color: var(--grey-color);
  text-decoration: underline;
}
.review__card-btn {
  display: flex;
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
}

.widget {
  position: fixed;
  left: 2%;
  bottom: 2%;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--light-color);
  border-radius: 20px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
  opacity: 0.8;
  text-align: center;
  width: 200px;
  padding: 15px;
}
.widget__rating-num {
  margin: 5px 0 3px;
}
.widget__rating-stars {
  margin-bottom: 7px;
}
.widget-modal {
  position: fixed;
  inset: 0;
  z-index: 3;
  width: 100%;
  background-color: var(--black2-color);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease-out, visibility 0.2s ease-out;
}
.widget-modal--active {
  opacity: 1;
  visibility: visible;
}
.widget-modal--active .widget-modal__window {
  left: 0;
}
.widget-modal__window {
  position: fixed;
  top: 0;
  left: -500px;
  z-index: 4;
  height: 100%;
  background-color: var(--light-color);
  max-width: 450px;
  overflow-y: auto;
  padding: 10px 8px;
  transition: left 0.2s ease-out;
}
.widget-modal__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}
.widget-modal__btn {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
}
.widget-modal__btn:hover {
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
}
.widget-modal__close {
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  width: 20px;
  height: 20px;
  margin: 0 8px;
}
.widget-modal__cards {
  justify-content: flex-start;
}
.widget-modal__card {
  width: 100%;
  min-height: 210px;
  max-width: inherit;
  transition: transform 0.2s;
}
.widget-modal__card:hover {
  transform: translate(0, -5px);
}
.widget-modal__card-left {
  gap: 8px;
}
.widget-modal__card-date {
  font-weight: 500;
  font-size: 12px;
  color: var(--grey-color);
  margin: 5px 0 0 0;
}
.widget-modal__card-name {
  font-size: 14px;
  font-weight: 700;
  color: var(--black-color);
}